
.carousel{
  width: 80%;
  margin: 5rem;
  margin-top: 0;
  margin-bottom: 0;
  justify-content: center;
  align-items: center;
  margin-left: 9rem;
}

.carousel img {
  transition: all ease 300ms;
  width: 100%;
  height: 500px;
  border-radius: 2rem;
  justify-content: center;
}
main aside{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 88vh;
}




.wordinglings{
  text-align: center;
  font-size: 1rem;
  margin-top: 0;
  margin-bottom:0;
  color: #f0d6d5;
}
.word{
  background: linear-gradient(#0766AD,#29ADB2);
  border-radius: 2rem;
  margin-bottom: 1rem;
  flex: 1;
  padding: 1rem;
  
}
.main {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}
.text{
  text-align: center;
  margin-left: 45%;
}
.btn {
display: block;
}
p.active{
  text-align: center;
  display: none;
}
h1.active{
  text-align: center;
  display: none;
}

.tellobee{
  margin-left: 63%;
}

.error{
  color: red;
}

.word__day{
 padding: 1rem;
  margin-top: .0rem;
  margin-bottom: 1.2rem;
  height: 200px;
  width: 100%;
  flex: 1;
  border-radius: 2rem 2rem;
  background: #a82050;
}


.word__day h2{
  text-align: center;
  color: antiquewhite;
  margin-top: 0;
  margin-bottom: 1.7rem;
  }

  h2{
    text-align: center;
    text-transform: capitalize;
  }
  
  .dark-mode {
    background-color: #333333;
    color: #ffffff;
  }
  

.quote p{
color: #f0d6d5;
text-align: center;
text-decoration: solid;
font-size: larger;
margin-top: 1.2rem;
margin-bottom: 1rem;
}
.result{
  text-align: center;
  font-weight: 400;
  font-size: 1.1rem;
}
@media screen and (max-width: 1024px){
.text{
  margin: 0;
  margin-left: 0;
}
.main{
  display: inline;
  margin-bottom: 1rem;
}
.tellobee{
  margin: 0;
}
.carousel{
  margin-left: 5rem;
}
}
